.header{
    background-color: #222;
    height: 36px;
    padding: 8px 32px 8px 16px;
    display: -ms-flexbox;
    display: flex;
}

.logo {
    background-color: #ff7f7f;
    height: 35px;
    -webkit-animation: rotate-logo infinite 8s linear;
            animation: rotate-logo infinite 8s linear;
    margin-right: auto;
}
  
@-webkit-keyframes rotate-logo {
    from {
      -webkit-transform: scaleX(-1);
              transform: scaleX(-1);
    }
}
  
@keyframes rotate-logo {
    from {
      -webkit-transform: scaleX(-1);
              transform: scaleX(-1);
    }
}


